import React, { PureComponent } from 'react';
import md5 from 'blueimp-md5';
import { Formik, Form, ErrorMessage } from 'formik';
// import { string, object } from 'yup';
// import mapValues from 'lodash/mapValues';
import debounce from 'lodash/debounce'

import FullpageVerticalCenter from 'components/FullpageVerticalCenter';
import Box from 'components/Box';
import Flex from 'components/Flex';
import Button from 'components/Button';
import Text from 'components/Text';
import Circle from 'components/Circle';
import Input from 'components/Input';
import Image from 'components/Image';
import Progress from 'components/Progress';
import ButtonYes from 'components/ButtonYes';
import ButtonNo from 'components/ButtonNo';
import Title from 'components/Title';
import Container from 'components/Container';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import StepNumber from './StepNumber'
import pen from './pen.svg'
import loading from './loading.svg'
import create from './create.svg'
import Layout from '../../Layout';
import withResponsive from '../../../hoc/withResponsive'

export const questions = [
  '過去或現在有重大疾病/手術紀錄？',
  '目前是否有正在服用的藥物？',
  '是否有家族病史？',
  '是否有任何藥物或食物過敏？',
  '是否有抽菸、輪班工作、酗酒等生活習慣？',
  '是否有偏好或感到焦慮的診療方式？如：您可能害怕打針',
]

export const nameId = questions.map((name) => md5(name))
const initialValue = nameId.reduce((iv, name) => Object.assign(iv, { [name]: '' }), {})

// const schema = object().shape(nameId.reduce((scm, name) => {
//   scm[name] = string().required('請務必填寫！');
//   return scm;
// }, {}))

class Step2 extends PureComponent {
  constructor(props) {
    super(props)
    this.handleSetAnswer = debounce(this.handleSetAnswer, 500).bind(this)
  }
  state = {}

  handleSetAnswer = (target) => {
    this.props.setAnswer(target)
  }

  handleChange = (formikOnChange) => e => {
    formikOnChange(e)
    this.handleSetAnswer(e.target)
  }

  handleGenerating = () => {
    this.setState({ generating: true })
    setTimeout(this.props.onNext, 1500)
  }

  render() {
    const { generating } = this.state
    const { answers, yesNo, isMobile, setAnswer, setYesNo } = this.props
    if (generating) {
      return (
        <Layout>
          <FullpageVerticalCenter>
            <Container textAlign="center">
              <Box display="inline-block" my="2em"><Image width="10em" src={loading} /></Box>
              <Text.serif color="titleBlue" fontSize="1.5em" fontWeight="bold">看診備忘錄生成中...</Text.serif>
            </Container>
          </FullpageVerticalCenter>
        </Layout>
      )
    }
    return (
      <Layout>
        <StepNumber>2</StepNumber>
        <Container py="3em" position="relative">
          <Progress step={2} />
          <Title title="簡述自己的特殊狀況" description="看診前先簡單自我檢測，讓醫生能更精準判斷該如何幫助你" icon={pen} />
          <Formik
            initialValues={Object.assign({}, initialValue, answers)}
            onSubmit={() => {
              setAnswer(answers)
              this.handleGenerating()
            }}
            // validationSchema={schema}
          >
            {({ handleChange, values }) => (
              <Form>
                <Flex
                  flexDirection={mobileOrDesktop('column', 'row')}
                  flexWrap="wrap"
                  alignItems="center"
                >
                {questions.map((q, index) => {
                  const name = nameId[index]
                  return (
                  <Box mt={mobileOrDesktop('1em', '2em')} width={mobileOrDesktop(1, 1/4)} key={index}>
                    <Box
                      mx={mobileOrDesktop('1em', '0.5em')}
                      boxShadow="-0px 3px 4px 4px rgba(0,0,0,0.1)"
                      bg="white"
                      borderRadius="1em"
                    >
                      <Flex
                        bg="questionBg"
                        px="1.5em"
                        py="1em"
                        borderRadius="1em 1em 0 0"
                        height="5em"
                        alignItems="center"
                        justifyContent="center"
                        fontSize={mobileOrDesktop('1.25em', '1em')}
                      >{q}</Flex>
                      <Box pt={yesNo[name] === undefined && !isMobile && '1.75em'} pb={yesNo[name] === undefined && !isMobile ? '1.75em' : '1em'} mb={yesNo[name] === undefined && !isMobile && '1px'}>
                        <Flex px="0.5em" mx={mobileOrDesktop('3em', '0.5em')}>
                          <ButtonYes
                            active={yesNo[name]}
                            onClick={() => {
                              setYesNo({ name, value: true })
                            }}
                          />
                          <ButtonNo
                            active={yesNo[name] === false}
                            onClick={() => {
                              setYesNo({ name, value: false })
                            }}
                          />
                        </Flex>
                        {yesNo[name] && (
                          <Box px="2em">
                            <Input
                              name={name}
                              value={values[name]}
                              placeholder="請輸入"
                              onChange={this.handleChange(handleChange)}
                              autoFocus
                            />
                          </Box>
                        )}
                        {yesNo[name] === false && !isMobile && (
                          <Box
                            textAlign="left"
                            px="2rem"
                            pt="1.25em"
                            mb="2px"
                            color="primary"
                          >
                            沒有
                          </Box>
                        )}
                        <ErrorMessage name={name}>
                          {msg => (
                            <Text mx="1em" color="danger">{msg}</Text>
                          )}
                        </ErrorMessage>
                      </Box>
                    </Box>
                  </Box>
                )})}
                <Box width={mobileOrDesktop(1, 1/4)} mt="2em" px={mobileOrDesktop('25%', '4%')}>
                  <Circle is={p => <Button.circle width="100%" type="submit" {...p} />}>
                    <Box ml="0.5em" width="80%">
                      <Image src={create} />
                    </Box>
                    <Text mt="0.5em">生成備忘錄</Text>
                  </Circle>
                </Box>
                </Flex>
              </Form>
            )}
          </Formik>
        </Container>
      </Layout>
    );
  }
}

export default withResponsive(Step2);
