import React from 'react';
import range from 'lodash/range';

import Box from './Box'
import Flex from './Flex'
import Text from './Text'
import Circle from './Circle'
import { mobileOrDesktop } from './ThemeProvider/theme';


const texts =['描述症狀', '用藥病史', '生成備忘錄']

const Progress = ({step, ...props}) => {
  return (
    <Flex justifyContent="center" mx={mobileOrDesktop('2em', 0)} {...props}>
      {range(3).map((num) => (
        <Box mx={mobileOrDesktop('1em', '2em')} width={mobileOrDesktop(1/3, 'auto')} key={num}>
          <Box width="2em" mx="auto" position="relative">
            <Circle color="white" bg={num + 1 === step ? 'titleBlue' : 'lightBgPurple'}>{num + 1}</Circle>
            {num < 2 && (<Box
              bg="lightBgPurple"
              width="7.5em"
              height="2px"
              position="absolute"
              top="50%"
              left="100%"
              transform="translateY(-50%)"
            />)}
          </Box>
          <Text textAlign="center" mt="0.75em" fontWeight="bold" color={num + 1 === step ? 'titleBlue' : 'lightBgPurple'}>{texts[num]}</Text>
        </Box>
      ))}
    </Flex>
  );
};

export default Progress;
