import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import { createSelector, createStructuredSelector } from 'reselect'

import {
  newDot,
  setDotData,
  removeDot,
  setAnswer,
  setYesNo,
  clearData,
} from './creatingReducer';

const key = 'creating'

const mapStateToProps = (state) => ({
  dots: state[key].get('dots').toJS(),
  yesNo: state[key].get('yesNo') ? state[key].get('yesNo').toJS() : {},
  answers: state[key].get('answers').toJS(),
});

const mapDispatchToProps = (dispatch) => bindActionCreators({
  newDot,
  setDotData,
  removeDot,
  setAnswer,
  setYesNo,
  clearData,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps);
